.contact-edit-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  background-color: rgb(255, 255, 255);
}
.contact-edit-page-container {
  display: flex;
  height: 400px;
  gap: 50px;
  position: relative;
}
.contact-edit-page-avatar img {
  width: 400px;
  height: 400px;
  border-radius: 15px;
}
.contact-edit-page-contact-details {
  width: 400px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  position: relative;
}
.contact-edit-page-contact-details label {
  font-size: 18px;
  position: absolute;
  left: 0;
  margin-top: 15px;
  margin-left: 10px;
  word-break: break-all;
}
.contact-edit-page-contact-details input {
  margin-top: 15px;
  height: 40px;
  margin-right: 10px;
  background-color: rgba(239, 239, 239, 0.542);
  border-radius: 5px;
  width: 270px;
  outline: none;
  padding: 10px;
}
.contact-edit-page-save-button button {
  height: 60px;
  width: 200px;
  margin-top: 10px;
  background-color: inherit;
  border-radius: 15px;
  border: 1px solid rgb(8, 26, 81);
  color: rgb(8, 26, 81);
  transition: 0.5s;
  font-size: 19px;
  left: 0;
  position: absolute;
}
.contact-edit-page-save-button svg {
  margin-right: 10px;
}
.contact-edit-page-save-button button:hover {
  background-color: rgb(8, 26, 81);
  color: white;
  border: none;
}
.contact-edit-page-cancel-button button {
  height: 60px;
  width: 160px;
  margin-top: 10px;
  background-color: inherit;
  border-radius: 15px;
  border: 1px solid rgb(8, 26, 81);
  color: rgb(8, 26, 81);
  transition: 0.5s;
  font-size: 19px;

}
.contact-edit-page-cancel-button svg {
  margin-right: 10px;
}
.contact-edit-page-cancel-button button:hover {
  background-color: rgb(8, 26, 81);
  color: white;
  border: none;
}
